  --macaw-warm-black-color: #10474a;
  --macaw-black-color: #000;
  --macaw-white-color: #fff;
  --macaw-blue-color: #226ce0;
  --macaw-gold-color: #e9b872;
  --macaw-body-bg-color: var(--macaw-skobeloff-color);
  --macaw-body-text-color: var(--macaw-white-color);
  --macaw-link-text-color: var(--macaw-gold-color);
  --macaw-code-bg-color: var(--macaw-black-color);
  --macaw-code-text-color: var(--macaw-white-color);
  --macaw-hr-border-color: var(--macaw-warm-black-color);
}

